.FloatingButton {
  height: 50px;
  width: 50px;
  background: #2fb454;
  border-style: solid;
  border-width: 0px;
  color: #ffffff;
  border-color: #000000;
  border-radius: 25px;
  cursor: pointer;
  line-height: 50px;
  font-size: 25px;
  text-align: center;
  position: absolute;
  top: 0px;
  left: 0px;
  -webkit-transition-duration: 0.5s;
       -o-transition-duration: 0.5s;
          transition-duration: 0.5s;
  outline: none;
  /*z-index: 1000;*/ }
  .FloatingButton > img {
    width: 100%; }

.active > .FloatingButton {
  background: white; }

.icon {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.icon-holder {
  width: 50px;
  height: 50px;
  position: relative; }

.menu-text {
  display: block;
  position: absolute;
  left: 0px;
  top: 0px;
  padding-left: 50px;
  border-radius: 25px;
  max-width: 0px;
  overflow: hidden;
  -webkit-transition-duration: 0.5s;
       -o-transition-duration: 0.5s;
          transition-duration: 0.5s;
  background: inherit; }



